import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { HallsDataService } from '../../../../../costumers/halls-data-service';
import { Observable, of } from 'rxjs';
import { HallType } from '../../../../../costumers/hall-type';
// import { FormGroup, FormControl, Validators } from '@angular/forms';
import { HttpHeaders } from '@angular/common/http';
import { HttpService } from '../../../../../http.service';
import { find, first } from 'rxjs/operators';
import { CanDeactivateComponent } from '../../../../../can-deactivate-guard.service';
import { FormProccesorService } from '../../../../../costumers/form-proccesor.service';
//  declare let $:any;


@Component({
  selector: 'app-media-edit',
  templateUrl: './media-edit.component.html',
  styleUrls: ['./media-edit.component.css']
})
export class MediaEditComponent implements OnInit, CanDeactivateComponent {

  /* **************************** */
  costumer: HallType;
  galleries: any;
  videos: any;

  /* **************************** */
  private apiKey;
  private guard = [];
  arrayFlies: any = [];
  masseges = [];
  filesSize: number = 0;

  isTrue: Observable<boolean>;

  constructor(private router: Router, private halls: HallsDataService, private http: HttpService, private valForm: FormProccesorService) { }

  ngOnInit() {

    this.halls.costumerObsever.pipe(
      find((val) =>  {return val['id']})
      ).subscribe(cost => {
    
      let cId = (cost && cost["user_id"]) ? cost["user_id"] : false;
      let uId = this.http.authUser["id"];

      console.log('costumerId: '+ cId + " userId "+ uId);
      if(cId === uId){
        let id = cost.id;
        let gal = this.halls.getGallery(id);
        this.galleries = gal['image'];
        this.videos = gal['video'];
        this.costumer = cost;
        this.apiKey = this.http.getApiKey();
        this.isTrue = of(true);
      }else{
        // let state = decodeURIComponent(this.router.url).split("/");
        // let media = "/"+state[1]+"/"+cost["company"]+"/media";
        this.isTrue = of(false);
        // console.log(media);
        // this.router.navigate([media]);
      }

    });
  }

  canDeactivate(): Observable<boolean> | Promise<boolean> | boolean {
    if(this.filesSize >= 1 || this.arrayFlies.length >= 1 || this.arrayFlies.guard >= 1){
      return confirm("לא שמרתה את הפרטים. תרצה לעזוב דף זה בכל זאת?")
    }else{
      return true;
    }
  }

  onSubmit() {

    let input: FormData = new FormData();
    let posterUrl = "http://ethio:8080/api/auth/costumers";
    let formInputs = this.costumer;
    let urls: string = formInputs['businessType'] + "/" + formInputs['company'];
    let checkFilesSizes = (Math.round(this.filesSize / Math.pow(1024, 2)) > 6) ? true : false;
    let errors: any = {};
    this.masseges = [];
    let counter = 0;

    // console.log(this.filesSize + " : " +Math.round(this.filesSize / Math.pow(1024,2) ));
    // console.log(this.filesSize + " : " +Math.round(Math.pow(1024,2)* 6 ) / (Math.pow(1024,2)));
    for (let ii of this.arrayFlies) {

      if (counter <= 3 && ii.target === "galleries") counter++;
      let extractTargetName = urls + "/" + ii.target + "/" + ii.name.split('.')[0];
      (ii.target === "loggo") ? formInputs['loggo'] = extractTargetName + '.' + ii.name.split('.')[(ii.name.split('.').length) - 1] : null;
      input.append(extractTargetName, ii, ii.name);
    }

    errors.galleries = (counter < 3) ? errors.galleries = "גלריית התמונות חייב להכיל לפחות 3 תמונות" : null;
    errors.fileSize = (checkFilesSizes) ? errors.fileSize = this.formatBytes(this.filesSize) + " : " + "גודל הקבצים גדולה מדי." : null;
    errors.loggo = (typeof formInputs['loggo'] === 'undefined' || !formInputs['loggo']) ? errors.loggo = "העלה תמונת לוגו או אייקון של החברה." : null;

    for (let ii in errors) {
      if (errors.hasOwnProperty(ii)) {
        if (errors[ii]) this.masseges.push(errors[ii]);
      }
    }
    console.log(this.masseges);


    if (this.masseges.length === 0) {

      this.halls.storeData(posterUrl,
        input, {
          headers: new HttpHeaders({
            'Authorization': 'Bearer ' + this.apiKey,
          })
        })
        .subscribe(evt => {
          console.log(evt);
          this.masseges.push(evt["massege"]);

          setTimeout(() => {
            // this.router.navigate(["/halls_events"]);
          }, 5000);

        }, (err) => {
          
          if (err["status"] === 401) {
            this.http.nextIslogged(false);
            window.sessionStorage.removeItem('user_key');
            this.router.navigate(["/login"]);
          }
        });
    }
  }

  formatBytes(a) {
    if (0 === a) return "0 Bytes";
    var c = 1024, d = 2,
      e = ["Bytes", "KB", "MB", "GB", "TB", "PB", "EB", "ZB", "YB"],
      f = Math.floor(Math.log(a) / Math.log(c));
    return parseFloat((a / Math.pow(c, f)).toFixed(d)) + " " + e[f];
  }

  selectedFiles(event, elemTarget) {

    if (!event.target.files[0] || this.guard.indexOf(elemTarget) >= 0) {
      console.log("You cant uplaod twise " + elemTarget);
      return;
    }
    if (elemTarget !== "galleries" && this.guard.indexOf(elemTarget) == -1) this.guard.push(elemTarget);
    let files = event.target.files;

    // let videoType = ['video/3gpp', 'video/H261', 'video/H263', 'video/H264', 'video/JPEG', 'video/mp4', 'video/mpeg'].indexOf(files[0].type);
    // let imageType = ['image/png', 'image/jpeg', 'image/gif'].indexOf(files[0].type);
    console.log(files);
    for (let theFile of files) {
      let elemName = theFile.name.split('.')[0];
      theFile.id = elemName + '_' + theFile.size;
      theFile.target = elemTarget;

      if (!this.fileContains(theFile)) {
        let targetElement = event.target.parentElement.nextElementSibling;

        this.filseReader(theFile).then(res => {

          targetElement.appendChild(this.createElements(res));

        },
          (error) => {
            console.log(error);
          });
      }
    }
  }

  private fileContains(theFile) {

    let isTrueOrFalse: any = false;
    let gal = this.arrayFlies;

    for (let idx = 0, len = gal.length; idx < len; idx++) {

      if (gal[idx].id === theFile.id) {
        isTrueOrFalse = true;
      }
    }
    return isTrueOrFalse;
  }

  unSelectFiles(evt) {

    let aTag = evt.target.parentElement;
    let div = aTag.parentElement;
    let parent = div.parentElement;
    let childrens = parent.children;
    let index = aTag.getAttribute('data-target');

    for (let ii = 0, len = childrens.length; ii < len; ii++) {

      if (childrens[ii] && aTag.id === childrens[ii].id) {
        let subtractSize = childrens[ii].id.split('_');
        let posSubtrat = subtractSize[subtractSize.length - 1];
        index = (index == "loggo") ? this.guard.indexOf(index) : (index == "video") ? this.guard.indexOf(index) : null;

        this.isExsist(childrens[ii]);
        if (index === 0 || index >= 0) {
          this.guard.splice(index, 1);
        }

        this.filesSize -= posSubtrat;
        parent.removeChild(childrens[ii]);
        break;
      }
    }
  }

  private createElements(elem) {

    let div = document.createElement('DIV');
    let aTag = document.createElement('A');
    let span = document.createElement('SPAN');
    let elemType = elem.type.split("/")[0];

    aTag.id = elem.id;
    if (elem.target === "loggo" || elem.target === "video") aTag.setAttribute('data-target', elem.target);
    div.id = elem.id;
    div.className = "d-inline";
    this.arrayFlies.push(elem);
    this.filesSize += elem.size;
    aTag.className = "close bg-secondary";

    span.setAttribute('aria-hidden', 'true');
    span.innerHTML = '&times';
    aTag.addEventListener("click", this.unSelectFiles.bind(this));
    aTag.appendChild(span);
    div.appendChild(aTag);

    if (elemType === "image") {

      let img = new Image();
      img.src = elem.src;
      img.className = "py-1 border border-info rounded";
      img.setAttribute("height", "100");
      img.setAttribute("alt", elem.type);
      div.appendChild(img);

    } else if (elemType === "video") {

      let video = document.createElement('video');
      let source = document.createElement('source');
      video.controls = true;
      video.height = 100;
      source.type = elem.type;
      source.src = elem.src;
      video.appendChild(source);
      div.appendChild(video);
    }
    return div;
  }

  private filseReader(elem) {

    var reader = new FileReader();
    return new Promise((rs, rj) => {

      reader.onload = (event: any) => {
        if (event.target.readyState === 2) {
          elem.src = event.target.result;
          rs(elem);
        }

        setTimeout(() => {
          if (event.target.readyState < 2) {
            rj("your file damaged");
          }
        }, 3000);
      };
      reader.readAsDataURL(elem);
    });
  }

  reset() {
    location.reload();
  }
  close() {
    this.router.navigate(['../']);
  }

  private isExsist(theFile) {

    let isTrueOrFalse: any = false;
    let gal = this.arrayFlies;

    for (let idx = 0, len = gal.length; idx < len; idx++) {

      if (gal[idx].id === theFile.id) {
        // console.log(gal[idx].id + " : " + theFile.id);
        this.arrayFlies.splice(idx, 1);
        break;
      }
    }
    return isTrueOrFalse;
  }

}
