import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class FormProccesorService {

  constructor() { }


  validate(inputControllers, costumer, key?) {

    let truly = true;
    let err = {};
    let success = {};

    if (key) {
      let inputs = this.checkInputs(inputControllers, key, costumer);
      let inp = inputs['input'];
      let status = inputs['status'];
      
      success[key] = inp[key];
      err[key] = inp;

      return {
        'success': status ? success : {},
        'errors':  status ? success : err,
        'status': status
      };

    } else {
      for (let ii in inputControllers) {

        let input = this.checkInputs(inputControllers[ii], ii, costumer);
        let inputs = input['input'];
        if (!input.status) {truly = false;}

        for (let ii in inputs) {
          if (ii != 'type') err[ii] = inputs;
        }
        if (input.status) {

          for (let idx in inputs) {
            if (idx != 'type') success[idx] = inputs[idx];
          }
        }
      }//END FOR IN LOOP
      return {
        'success': success,
        'errors': err,
        'status': truly
      };
    }


  }

  checkInputs(input, ii, costumer) {
    let gnKey = {
      company: "שם חברה",
      contact: "איש קשר",
      email: "אימייל",
      tel: "טלפון",
      address: "כתובת",
      title: "תיאור או כותרת",
      discription: "אודות החברה"
    };

    let inputs = {};
    let isTrue = true;
    let transformHebKey = {};
    if (!gnKey[ii]) {
      return {
        'status': false,
        'input': transformHebKey[ii] = "שגיעה"
      }//isTrue?inputs:transformHebKey
    }
    if (input.touched && input.valid && input.dirty && costumer[ii] != input.value) {
      inputs[ii] = input.value;
      inputs['type'] = "success";
    } else if (costumer[ii] === input.value) {
      //console.log(gnKey[ii]);
      isTrue = false;
      transformHebKey['type'] = "warning";
      transformHebKey[ii] = gnKey[ii] + ": לא ביצעת שינויים כל שהם.";
    } else {
      //onsole.log(gnKey[ii]);
      isTrue = false;
      transformHebKey['type'] = "danger";
      transformHebKey[ii] = gnKey[ii] + ": לא תקף.";
    }
    return {
      'status': isTrue,
      'input': isTrue ? inputs : transformHebKey
    }//isTrue?inputs:transformHebKey;
  }
}
