
import { Component, OnInit } from '@angular/core';
import { HallType } from '../../../../costumers/hall-type';
import { ActivatedRoute } from '@angular/router';
import { HallsDataService } from '../../../../costumers/halls-data-service';
import { Observable, of } from 'rxjs';
import { first } from 'rxjs/operators';
import { HttpService } from '../../../../http.service';
declare var $: any;

@Component({
  selector: 'app-hall-media',
  templateUrl: './hall-media.component.html',
  styleUrls: ['./hall-media.component.css']
})
export class HallMediaComponent implements OnInit {
  costumer: Observable<HallType>;;
  galleries:any;
  videos:any;

  constructor(private route: ActivatedRoute, private hall: HallsDataService, private http: HttpService) { }

  ngOnInit() {
  
    this.hall.costumerObsever.pipe(first()).subscribe(data =>{
      // console.log(data);
      let id = data.id;
      this.costumer = of(data);
      let gal = this.hall.getGallery(id);
      this.galleries = gal['image'];
      this.videos = gal['video'];

      $('.carousel').carousel();
    });

  }
  galActiveItem(ii){
    
    $(".carousel-indicators")[0].children[ii].click();
  }

  vidActiveItem(ii){
    
    //$(".carousel-indicators")[0].children[ii].click();

  }

}
