import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'splitText'
})
export class SplitTextPipe implements PipeTransform {

  transform(value: any, args?: any, pos?:any ): any {
    
    if(value){
      let spliter = args? args: ' ';
    let split = value.split(spliter);
    let defaultPos = (split.length - 1);
    let paramPos = (pos || pos === 0)?pos: defaultPos;
    // console.log(pos);
    let resualt = paramPos? split[paramPos]: paramPos;

    return resualt;
    
    }
    return value;
    
  }

}

